

.img-result{
  border-radius: 5%;
  margin: 10px;
}
header{
  display: flex;
  justify-content: space-around;
  position: fixed;
  border-bottom: 1px lightgrey solid;
  height: 60px;
  width: 100%;
  background-color: white;
  top:0px;
  padding-top:10px;
  padding-bottom: 5x;
  z-index: 500;
font-family: sans-serif;
color:grey;


}
header i{
  font-size: 1.5em;
  color:grey;
}
.pinterest{
  font-size: 2em;
  color: red;
  margin-right: 15px;
}
header span{
  padding: 20px;
  font-weight: bold;
  display: flex;
  text-align: right;
}

.inicio{
  color: black;
}

.inicio:hover{
  color:grey;
}

.siguiendo:hover{
  color: black;
}


.input-text{
  background-color: #efefef;
  border: 0;
  border-radius: 5px;
  color: #333;
  font-size: 16px;
  font-weight: 600;
  height: 75%;
  outline: none;
  width: 100%;
  padding-top: 10px;
  /* padding-left: 16px;
  padding-right: 16px; */
}
.fa-search{
  padding: 15px;

}

.all-items{
  display: flex;
  justify-content: space-around;
  padding: initial;
}


.container-result{
  margin:0 auto;
  width: 90%;
  position: relative;
  padding-top: 80px;
  -webkit-column-count: 3;
  -webkit-column-gap: 10px;
  -webkit-column-fill: auto;
  -moz-column-count: 3;
  -moz-column-gap: 10px;
  -moz-column-fill: auto;
  column-count: 4;
  column-gap: 5px;
  column-fill: auto;

}
.container-result .img-result:hover{
  opacity: 0.6;
  cursor: zoom-in;

}

/*modal*/

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 990;
  height: 100vh;
  overflow-x: scroll;

  
}

.modal-main {
  position:fixed;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}
.display-block {
  display: block;
}

.display-none {
  display: none;
}
.img-style{
display: flex;
margin: auto;
padding-top: 20px;
margin-bottom: 20px;
border-radius: 25px;
overflow: auto;
}

.img-style:hover{
  opacity: 0.7;
  cursor: pointer;
}
.fa-times{
  font-size: 1.5em;
}
.button-group{
  display: flex;
  justify-content: space-around;
  margin-top: 10px;

  
}

.btn-enviar, .btn-guardar{
  font-size: medium;
  font-weight: 600;
  display: flex;
  flex-direction: row;
  border-style: none;
  width: 100px;
  height: 40px;
  justify-content: space-evenly;
  cursor: pointer;
  border-radius: 8px;
  
}
.btn-guardar{
  background-color: red;
  color: white;
}
.btn-enviar:hover{
  opacity: 0.3;
}
.btn-guardar:hover{
  opacity: 0.3;
}

